<template>
  <div>
    <vh-page :options="options">
      <div slot="header-right" @click="click">提交</div>
      <router-link to="/Hello-3" tag="a">
        <h1 v-for="i in 15" :key='i'>{{ msg }}</h1>
      </router-link>
    </vh-page>
  </div>
</template>

<script>
export default {
  name: 'demo-2',
  data () {
    return {
      msg: 'Hello Vue 2!',
      options: {
        back: true,
        backgroundColor: '#eee',
        header: {
          title: '页面-2',
          back: true,
          backgroundColor: '#fff',
          color: '#000'
        }
      }
    }
  },
  forward () {
    // Object.assign(this.$data, this.$options.data())
  },
  components: {},
  methods: {
    click () {
      this.msg = 'Hello Vue 2-2!'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  color: #42b983;
}
</style>



